import * as faceapi from "face-api.js";

function setupCamera() {
    feather.replace();

    const changeCamera = document.getElementById('changeCamera');
    const video = document.getElementById('my-video');

    const constraints = {
        video: {
            width: {
                min: 1280,
            },
            height: {
                min: 720,
            },
        }
    };

    async function getCameras() {
        const devices = await navigator.mediaDevices.enumerateDevices();
        const videoDevices = devices.filter(device => device.kind === 'videoinput');
        return videoDevices;
    }

    async function bindVideo() {
        let currentCameraId = 0;
        const availableCameras = await getCameras();
        changeCamera.onclick = () => {
            currentCameraId = (currentCameraId + 1) % availableCameras.length;
            const updatedConstraints = {
                ...constraints,
                deviceId: {
                    exact: availableCameras[currentCameraId].deviceId,
                }
            };

            startStream(updatedConstraints);
        };
        startStream({
            ...constraints,
            deviceId: {
                exact: availableCameras[0].deviceId,
            }
        });
    }

    const doScreenshot = () => {
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        canvas.getContext('2d').drawImage(video, 0, 0);
        screenshotImage.src = canvas.toDataURL('image/webp');
        screenshotImage.classList.remove('d-none');
    };

    async function startStream(constraints) {
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        video.srcObject = stream;
    }

    bindVideo();

}

function setupFaceRecognition() {
    const input = document.getElementById('my-video');

    async function detect(input) {
        const detections = await faceapi.detectAllFaces(input);
        console.log(detections);
    }

    async function detect_loop(input) {
        while (true) {
            await detect(input);
        }
    }

    detect_loop(input);
}

document.addEventListener("DOMContentLoaded", setupCamera);
document.addEventListener("DOMContentLoaded", setupFaceRecognition);